body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.navbar
{
  margin-bottom:30px;
}
.active-cyan-4 input[type=text]:focus:not([readonly]) {
  border: 1px solid #2692f7;
  box-shadow: 0 0 0 1px #2692f7;
}
.active-cyan-3 input[type=text] {
  border: 1px solid #2692f7;
  box-shadow: 0 0 0 1px #2692f7;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}
.background{
  background: #0F2027;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, rgb(29, 60, 74), #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
   min-height: 100vh;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.clickable {
  cursor: pointer;
}

.main-header{
  color: rgb(9, 9, 9);
  font: 50px italic;
  margin-bottom: 20px;

  -webkit-animation-name: drop-top;

          animation-name: drop-top;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

@-webkit-keyframes drop-top {
  0%{
    opacity: 0;
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
  }
  60% {
    opacity: 1;
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes drop-top {
  0%{
    opacity: 0;
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
  }
  60% {
    opacity: 1;
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}
#split-line {
  width:1px;
  background-color:rgb(23, 23, 23);
  position: absolute;
  height: 100%;
}
